const apiKey = 'nKeKCizQzQ1pFWy0MO3liI2li0LlMJjXZQO6xHOLoBIgOo1y_LfzC4zr2XMBpW7RQ7LMx_ebOUVfYwKwrKyj0DnyY4LJbb5NSD6r6jsIGLyaVwpDuYkpq1C3YIA6YXYx';

//visit https://cors-anywhere.herokuapp.com/corsdemo to bypass CORS Restriction and click "Request temporary access to the demo server"
const Yelp = {
    search(term, location, sortBy){
        return fetch(`https://cors-anywhere.herokuapp.com/https://api.yelp.com/v3/businesses/search?term=${term}&location=${location}&sort_by=${sortBy}`, {
            headers: {
                Authorization: `Bearer ${apiKey}`
            },
        }).then((response) => {
            return response.json();
        }).then((jsonResponse) => {
            if (jsonResponse.businesses){
                return jsonResponse.businesses.map(business => ({
                        id: business.id,
                        imageSrc: business.image_url,
                        name: business.name,
                        address: business.location.address1,
                        city: business.location.city,
                        state: business.location.state,
                        zipCode: business.location.zip_code,
                        category: business.categories[0].title,
                        rating: business.rating,
                        reviewCount: business.review_count
                    }));
                }
              });
            }
          };

export default Yelp;

